import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  // {
  //   path: '/',
  //   redirect: '/login'
  // },
  {
    path: '/login',
    component: () => import('@/views/LoginIndex.vue')
  },
  {
    path: '/',
    component: () => import('@/views/LayoutIndex.vue'),
    children: [
      {
        path: '/home',
        component: () => import('@/views/home/HomeIndex.vue')
      }
    ]
  }

]

const router = new VueRouter({
  routes
})

export default router
